.cartContainer {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.button-close {
  width: 100%;
  height: 50px;
  text-align: right;
}

.close {
  width: 50px;
  height: 50px;
  color: rgb(228, 92, 92);
  cursor: pointer;
}
.items-container ul {
  margin: 0;
  padding: 0;
}
.item-carts {
  width: 80%;
  margin: 0 auto;
  height: 200px;
  display: flex;
  font-size: 1rem;
  align-items: center;
  gap: 10px;
  border-bottom: 1px solid white;
  background: #000;
}

.item-carts p {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  color: white;
}

.item-carts p span {
  display: flex;
  align-items: center;
  justify-content: center;
}

.imageCart {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  overflow: hidden;
  background-size: cover;
}

.imageCart img {
  width: 100%;
  height: 100%;
}

.totalCartContainer {
  width: 100%;
  height: 50px;
  background-color: white;
  font-size: 1.5rem;
  display: flex;
  align-items: center;
  margin-top: 10px;
}

.totalCartContainer p {
  width: 80%;
  text-align: center;
  color: black;
  font-weight: 800;
  margin: 0 auto;
}
