.container {
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  background-color: white;
  margin-top: 20px;
  gap: 20px;
}

.prod-cards {
  width: 400px;
  height: 400px;
  color: rgb(27, 12, 12);

  padding: 10px;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  cursor: pointer;
  transition: 0.5s ease;
}
.prod-cards:hover {
  transform: scale(1.04);
}

.image {
  width: 100%;
  height: 200px;
  background-size: cover;
  background-position: center;
}

.image img {
  width: 100%;
  height: 100%;
}

.price {
  display: flex;
  justify-content: space-between;
  font-size: 20px;
  font-weight: bold;
}
.container h1 {
  text-align: center;
  font-size: 1.2rem;
}
.container h1,
p {
  margin: 0;
}

.container p {
  font-size: 1rem;
}

.price-container {
  display: flex;
  gap: 10px;
}
.price-container del {
  color: red;
}

.avail {
  background-color: rgb(0, 128, 0, 0.5);
  width: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
}

.ratings-stocks {
  width: 150px;
}
.ratings-star {
  padding: 0;
  margin: 0;
  display: flex;
  gap: 5px;
}

.ratings-star li {
  margin: 0;
  padding: 0;
}

.star-bg {
  color: rgb(129, 110, 3);
}

.link-container {
  text-decoration: none;
}
