.app-container {
  width: 100%;
  height: inherit;
  background-color: rgb(157, 177, 207);
  overflow-y: scroll;
}

header {
  background-color: black;
  width: 100%;
}
.left-section {
  width: 100%;
  height: 100%;
  background-color: white;
  display: flex;
}
.cart-container {
  transition: 0.5s ease;
}

.cart-container.active {
  background: #000;
  width: 25%;
}
.page-item-container.active {
  width: 100%;
}

.page-item-container.hide {
  display: none;
}
