.top-container {
  width: 80%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;

  color: orange;
}

.search-container {
  width: 1200px;
  display: flex;
  align-items: center;
  gap: 5px;
}
input {
  height: 50px;
  width: 100%;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  padding-left: 20px;
  font-size: 1.5rem;
}

.search {
  width: 40px;
  height: 40px;
  background-color: rgb(20, 24, 24);
  color: orange;
  cursor: pointer;
}

.usericon,
.cartIcon {
  width: 30px;
  height: 30px;
}
.cart-container {
  width: 80px;
  display: flex;
}
.cart-container p {
  color: white;
}
